<template>
  <div>
    <div class="header-spacer"></div>
    <div class="container-fluid">
      <div class="row">
        <div class="col col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12 no-padding">
          <div class="ui-block" ref="avatarUpload">
            <div class="top-header top-header-favorit mb-down" style="margin: 0 !important;">
              <div class="top-header-thumb">
                <img src="/image/friend-groups-bg.png" alt="nature" />
                <div class="top-header-author">
                  <div class="author-thumb">
                    <img
                      v-if="isOtherUser === false"
                      :src="getAvatarLocal()"
                      class="user-avatar"
                      alt="author"
                    />
                    <img v-else :src="getOtherAvatar()" class="user-avatar" alt="author" />
                    <div v-if="isOtherUser === false" class="upload_option">
                      <a href="javascript:;" @click="getImageProfile()" class="btn btn-control bg-breez">
                        <svg class="olymp-chat---messages-icon">
                          <use xlink:href="/svg-icons/sprites/icons.svg#olymp-camera-icon" />
                        </svg>
                      </a>
                      <form enctype="multipart/form-data">
                        <input
                          type="file"
                          style="display:none"
                          id="getImageProfile"
                          @change="previewImage"
                          name="image"
                        />
                      </form>
                    </div>
                  </div>
                  <div class="author-content">
                    <router-link
                        v-if="isOtherUser === false"
                      :to="'/profile/' + $store.state.user.username"
                      class="h3 author-name"
                    >
                        <span v-if="getUserDetails.user_type === '1'">
                            {{ getLocalData().firstname === null || getLocalData().firstname === undefined ? getLocalData().username : getLocalData().firstname + ' ' + getLocalData().lastname }}
                        </span>
                        <span v-else-if="getUserDetails.user_type === '2' ">
                            {{ getLocalData().business_name === null || getLocalData().business_name === undefined ? getLocalData().username : getLocalData().business_name }}
                        </span>
                    </router-link>
                    <router-link
                        v-else
                      :to="'/profile/' + $store.state.user.username"
                      class="h3 author-name"
                    >
                        <span v-if="type === 1">
                            {{ firstname === null || firstname === undefined ? username : firstname + ' ' + lastname }}
                        </span>
                        <span v-else-if="type === 2 ">
                            {{ business_name === null || business_name === undefined ? username : business_name }}
                        </span>
                    </router-link>

                    <div class="country" v-if="$store.state.user.club === null ">No Club</div>
                    <div class="country" v-else>{{$store.state.user.club}}</div>
                  </div>
                </div>
              </div>
              <div class="profile-section">
                <div class="row">
                  <div class="col col-xl-8 m-auto col-lg-8 col-md-12">
                    <canvas style="display:none" id="canvas"></canvas>
                    <ul class="profile-menu">
                      <li v-bind:class="{active: isActive} ">
                        <span @click="showReview('all')">Timeline</span>
                      </li>
                      <li v-bind:class="{active: isActive}">
                        <span @click="showReview('photos')">Photos</span>
                      </li>
                    </ul>
                  </div>
                </div>

                <div v-if="isOtherUser === false" class="control-block-button">
                  <a href="#" class="btn btn-control bg-mdd">
                    <svg class="olymp-chat---messages-icon">
                      <use xlink:href="/svg-icons/sprites/icons.svg#olymp-camera-icon" />
                    </svg>
                  </a>

                  <router-link :to="{name: 'AccountInfo'}" class="btn btn-control bg-purple">
                    <svg class="olymp-chat---messages-icon">
                      <use xlink:href="/svg-icons/sprites/icons.svg#olymp-settings-v2-icon" />
                    </svg>
                  </router-link>

                  <router-link :to="{name: 'PasswordChange'}" class="btn btn-control bg-orange d-sm-block d-md-none">
                    <svg class="olymp-chat---messages-icon">
                      <use xlink:href="/svg-icons/sprites/icons.svg#olymp-settings-v2-icon" />
                    </svg>
                  </router-link>

                </div>

                <div v-else class="control-block-button">
                  <span v-if="signedIn === true">
                    <button v-if="is_following === false" @click="connection()" class="btn btn-lg bg-mdd following">Follow</button>
                    <button v-else @click="connection()" class="btn btn-lg bg-mdd unfollowing">Unfollow</button>
                  </span>
                </div>

              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import avta from "@/functions/getAvatar";
import ls from "../../localstorage";

var pageData = {
  projectName: "",
  userData: {},
  id: null,
  avatar: "",
  signedIn: false,
  errorData: {},
  isActive: false,
  isOtherUser: false,
  firstname: "",
  lastname: "",
  username: "",
  business_name: "",
  is_following: false,
  other_id: "",
    type: "",
};
export default {
  name: "ProfileBanner",
  data() {
    return pageData;
  },
  computed: mapState({
    getAvatar: state => state.user.avatar,
    getUsername: state => state.user.username,
    getUserDetails: state => state.user,
  }),
  mounted() {
    this.getUserData();
    this.checkUserData();
    window.eventBus.$on("isOtherUser", val => {
      // // console.log('other user ',val);
      this.getUserData();
      this.isOtherUser = val;
    })
  },
  methods: {
    getLocalData() {
        let userData = ls.getStorage("fansData");
        userData = userData[0];
        // console.log(userData)
        return userData;
    },
    getOtherAvatar() {
      return avta({ local: { av: this.avatar, us: this.username } });
    },
    getAvatarLocal() {
      return avta({ state: { av: this.getAvatar, us: this.getUsername } });
    },

      checkUserData() {
          if (ls.checkStorage("fansData")) {
            this.signedIn = true;
          }else {
            this.signedIn = false;
          }
      },

    // process following and is following
    connection() {
      if (this.$store.state.user.username !== undefined) {
        if (this.is_following === false ) {
          // process follow
          $(".following").attr("disabled",true);
          $(".following").html('...');
          this.axios.post('user/follow/' + this.other_id)
                  .then(response => {
                    // console.log(response.data);
                    this.is_following = true;
                    $(".following").attr("disabled",false);
                    $(".following").html('Unfollow');
                    return response;
                  })
                  .catch(error => {
                    // console.log(error.response);
                    $(".following").attr("disabled",false);
                    $(".following").html('Follow');
                    return error;
                  })
        }else {
          // process unfollow
          $(".unfollowing").attr("disabled",true);
          $(".unfollowing").html('...');
          this.axios.post('user/unfollow/' + this.other_id)
                  .then(response => {
                    // console.log(response.data);
                    this.is_following = false;
                    $(".unfollowing").attr("disabled",false);
                    $(".unfollowing").html('Follow');
                    return response;
                  })
                  .catch(error => {
                    // console.log(error.response);
                    $(".unfollowing").attr("disabled",false);
                    $(".unfollowing").html('Unfollow');
                    return error;
                  })
        }
      }else {
        $("#registration-login-form-popup").modal("show");
      }
    },
    // get other users data
    getUserData() {
      // let loader = this.showLoading();
      var usernameRoute = this.$route.params.username;
      let user;
      if (ls.checkStorage("fansData")) {
        let userData = ls.getStorage("fansData");
        user = userData[0].username;
      }else {
        user = '';
      }

      if (user !== usernameRoute && usernameRoute !== undefined) {
        this.isOtherUser = true;
        if (this.signedIn === false) {
            this.axios
                .get("/other/detailsout/" + usernameRoute + "/1")
                .then(response => {
                    // loader.hide();
                    var responseData = response.data.data;
                    // console.log('other user data',responseData);
                    this.username = responseData.username;
                    this.business_name = responseData.business_name;
                    this.firstname = responseData.firstname;
                    this.lastname = responseData.lastname;
                    this.is_following = responseData.is_following;
                    this.other_id = responseData.id;
                    this.type = responseData.type;
                    if (responseData.avatar === undefined) {
                        this.avatar = responseData.logo;
                    }else {
                        this.avatar = responseData.avatar;
                    }
                })
                .catch(error => {
                    // loader.hide();
                    //   // console.log(error);
                    return error;
                });
        }else {
            this.axios
                .get("/other/detailsin/" + usernameRoute + "/1")
                .then(response => {
                    // loader.hide();
                    var responseData = response.data.data;
                    // console.log('other user data',responseData);
                    this.username = responseData.username;
                    this.business_name = responseData.business_name;
                    this.firstname = responseData.firstname;
                    this.lastname = responseData.lastname;
                    this.is_following = responseData.is_following;
                    this.other_id = responseData.id;
                    this.type = responseData.type;
                    if (responseData.avatar === undefined) {
                        this.avatar = responseData.logo;
                    }else {
                        this.avatar = responseData.avatar;
                    }
                })
                .catch(error => {
                    // loader.hide();
                    //   // console.log(error);
                    return error;
                });
        }
      }else {
        // loader.hide();
        this.isOtherUser = false;
      }

    },

    showReview(review) {
      window.eventBus.$emit("reviews", review);
      $(".bll").removeClass("active");
      $(this).addClass("active");
      this.isActive = !this.isActive;
    },
    showLoading() {
      let loader = this.$loader(this, this.$refs.avatarUpload, {
        loaderType: "spinner"
      });
      return loader;
    },
    // click the image input form
    getImageProfile() {
      $("#getImageProfile").click();
    },
    // update user avatar
    updateProfile() {
      var loader = this.showLoading();
      let file = $("#getImageProfile").get(0).files[0];
      let formData = new FormData();
      formData.append('image', file);


      if(this.getUserDetails.user_type === '1') {
        this.axios({
          method: "post",
          url: "/profile/update",
          data: formData,
          config: { headers: { "Content-Type": "multipart/form-data" } }
        })
                .then(response => {
                  loader.hide();
                  this.fetchUserDetails();
                  this.errorData = {
                    message: "Image Uploaded",
                    isError: "success"
                  };
                  return response;
                })
                .catch(error => {
                  loader.hide();
                  this.errorData = {
                    message: error.response
                            ? "An error from our end"
                            : "Sorry an error occured, check your internet",
                    isError: true
                  };
                });
      }else {
        this.axios({
          method: "post",
          url: "/profile/update",
          data: formData,
          config: { headers: { "Content-Type": "multipart/form-data" } }
        })
                .then(response => {
                  loader.hide();
                  this.fetchUserDetails();
                  this.errorData = {
                    message: "Image Uploaded",
                    isError: "success"
                  };
                  return response;
                })
                .catch(error => {
                  loader.hide();
                  this.errorData = {
                    message: error.response
                            ? "An error from our end"
                            : "Sorry an error occured, check your internet",
                    isError: true
                  };
                });
      }
    },
    async previewImage() {
      if ($("#getImageProfile").get(0).files.length > 0) {
        if (
          $("#getImageProfile")
            .get(0)
            .files[0].name.split(".")
            .pop() !== "png" &&
          $("#getImageProfile")
            .get(0)
            .files[0].name.split(".")
            .pop() !== "jpg" &&
          $("#getImageProfile")
            .get(0)
            .files[0].name.split(".")
            .pop() !== "jpeg"
        ) {
          this.errorData = {
            message: "Sorry image can only be (Jpg - Png)",
            isError: true
          };
        } else {
          this.updateProfile();
        }
      }
    },
    fetchUserDetails() {
      // request to load user data
      window.eventBus.$emit("loadUserData", true);
    },

  }
};
</script>

<style>
.top-header-author .author-thumb {
  position: relative !important;
}
.upload_option {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  visibility: hidden;
  z-index: 5;
  top: 0;
  border-radius: 50%;
  transition: all 0.3s ease-in;
}
.top-header-author .author-thumb:hover .upload_option {
  visibility: visible;
}
.profile-menu li {
  cursor: pointer;
}
.profile-menu .active {
  color: black;
  font-weight: bold;
}
.user-avatar {
  width: 120px;
  height: 102px;
}
/* @media (max-width: 980px) {
  .mb-down {
    margin: 70px 0 0 0 !important;
  }
} */
@media (max-width: 680px) {
  .mb-down .user-avatar {
    width: 100%;
    height: auto !important;
  }
  .mb-down .top-header-author .author-thumb {
    width: 99px;
    height: 99px;
  }
}
@media (max-width: 490px) {
  .mb-down .top-header-author .author-thumb {
    width: 69px;
    height: 69px;
  }
}
</style>
